<style lang="stylus" type="text/stylus">
  #homePage {
    padding: 0 16px 16px
    overflow: auto

    .card {
      margin-top: 16px

      &.distance {
        .ivu-card-extra {
          top: 9px
        }

        .ivu-card-body {
          padding: 0
        }

        .out-location-ps {
          max-height calc(100vh - 187px)
          padding: 16px;
          border-bottom: 1px solid #e9eaec
          overflow: auto

          .out-location {
            margin-bottom: 8px

            &:last-child {
              margin-bottom: 0
            }

            .ivu-btn-group {
              display: flex
            }
          }
        }

        .in-location {
          display: flex
          padding: 16px;

          .left {
            flex 1
          }

          .right {
            width: 47px
            padding-left: 5px

            &.has-val {
              width: 87px
            }
          }
        }
      }

      &.filter {
        .ivu-card-extra {
          top: 9px
        }

        .filter-val {
          margin-top: 20px
        }
      }
    }
  }

</style>

<template>
  <div id="homePage" class="h" ref="homePagePs">
    <Row :gutter="16">
      <i-col :xs="24" :sm="24" :md="12" :lg="12">
        <Card class="card distance">
          <p slot="title">
            <Icon :size="iconSize" type="md-navigate"/>
            Location
          </p>
          <ButtonGroup slot="extra">
            <Button
              v-if="copyDistanceAll"
              v-clipboard:copy="copyDistanceAll"
              v-clipboard:success="copySuccess"
              type="primary">
              <Icon :size="iconSize" type="md-copy"/>
            </Button>
            <Button v-if="copyDistanceAll" @click="formatLocation([])" type="warning">
              <Icon :size="iconSize" type="md-trash"/>
            </Button>
            <Button @click="disSetting()">
              <Icon type="md-cog" :size="iconSize"/>
            </Button>
            <Modal
              v-model="distance.settingModal"
              title="Distance Settings"
              transfer
              ok-text="OK"
              @on-ok="changeLocation()"
              cancel-text="Cancel">
              <div style="border-bottom: 1px solid #e9e9e9;padding-bottom:6px;margin-bottom:6px;">
                <RadioGroup v-model="distance.outModal">
                  <Radio label="go">
                    <Icon type="md-navigate"></Icon>
                    <span>Go++</span>
                  </Radio>
                  <Radio label="pokeStopGPX">
                    <Icon type="md-laptop"/>
                    <span>pokeStopGPX</span>
                  </Radio>
                  <Radio label="pokemonGPX">
                    <Icon type="md-laptop"></Icon>
                    <span>pokemonGPX</span>
                  </Radio>
                  <Radio label="xcodePatrol">
                    <Icon type="md-laptop"></Icon>
                    <span>Xcode Patrol</span>
                  </Radio>
                </RadioGroup>
                <br>
                <Checkbox
                  :indeterminate="distance.indeterminate"
                  :value="distance.checkAll"
                  @click.prevent.native="disCheckAll">all
                </Checkbox>
              </div>
              <CheckboxGroup v-model="distance.checkAllGroup" @on-change="disCheckAllGroupChange">
                <Checkbox
                  v-for="(arr,arrIndex) in distance.checkArr"
                  :key="arrIndex"
                  :label="arr">{{arr}}
                </Checkbox>
              </CheckboxGroup>
              <!-- SN -->
              <i-input v-model.trim="distance.serialNumber" placeholder="Serial Number"><span
                slot="append">SN</span></i-input>
              <!-- SN -->
              <i-input v-model.trim="distance.toFixed" placeholder="toFixed Number"><span
                slot="append">toFixed</span></i-input>
              <!-- speed -->
              <i-input v-model.trim="distance.speed" placeholder="speed Number"><span
                slot="append">speed</span></i-input>


            </Modal>
          </ButtonGroup>
          <div id="outLocationPs" ref="outLocationPs" v-show="distance.outVal.length"
               class="out-location-ps">
            <ul class="out-ul">
              <li
                class="out-location"
                v-for="(outVal,outValIndex) in distance.outVal"
                :key="outValIndex">
                <ButtonGroup>
                  <Button v-clipboard:copy="outVal"
                          v-clipboard:success="copySuccess">
                    {{outVal}}
                    <!--<template v-if="!isIos">{{outVal}}</template>-->
                    <!--<a v-else target="_blank" :href="`pokego2://${outVal}`">{{outVal}}</a>-->
                  </Button>
                  <Button type="warning" @click="delOutVal(outValIndex)">
                    <Icon :size="iconSize" type="android-close"/>
                  </Button>
                </ButtonGroup>
              </li>
            </ul>
          </div>
          <div class="in-location">
            <i-input type="textarea" :rows="1" class="left"
                     @on-enter="addLocation(distance.inVal)"
                     v-model.trim="distance.inVal" clearable
                     placeholder="Enter something..."/>
            <ButtonGroup
              v-if="distance.inVal"
              class="right"
              :class="{'has-val':distance.inVal}"
              slot="append">
              <Button type="primary" @click="addLocation(distance.inVal)">
                <Icon :size="iconSize" type="android-add"/>
              </Button>
              <Button type="warning" @click="distance.inVal=''">
                <Icon :size="iconSize" type="ios-trash-outline"/>
              </Button>
            </ButtonGroup>
          </div>
        </Card>
      </i-col>

      <i-col :xs="24" :sm="24" :md="12" :lg="12">
        <Card class="card distance">
          <p slot="title">
            <Icon :size="iconSize" type="android-person-add"/>
            Friends
          </p>

          <Button slot="extra" type="primary"
                  v-clipboard:copy="friends.str"
                  v-clipboard:success="copySuccess">
            <Icon :size="iconSize" type="ios-copy"/>
            &nbsp;{{friends.data.length}}
          </Button>

          <div id="friendPs" class="out-location-ps">
            <ul class="out-ul">
              <li
                class="out-location"
                v-for="(friend,friendsIndex) in friends.data"
                :key="friendsIndex">
                <Button v-clipboard:copy="friend.number"
                        v-clipboard:success="copySuccess">
                  {{friend.default}}
                </Button>
              </li>
            </ul>
          </div>
        </Card>
      </i-col>

      <i-col :xs="24" :sm="24" :md="12" :lg="12">
        <Card class="card filter">
          <p slot="title">
            <Icon :size="iconSize" type="ios-color-filter-outline"/>
            Filter
          </p>
          <div slot="extra">
            <i-input style="width: 80px;" placeholder="Keywords..." v-model="filterObj.obj"/>
            <i-input style="width: 80px;" placeholder="Keywords..." v-model="filterObj.str"/>
            <Button @click="onFilter" ghost>
              <Icon :size="iconSize" type="ios-search"/>
            </Button>
          </div>
          <i-input type="textarea" :rows="4"
                   v-model="filterObj.data"
                   placeholder="Enter something..."/>
          <div v-show="filterObj.arr">QTY: {{filterObj.length}}</div>
          <i-input type="textarea" autosize
                   class="filter-val"
                   v-show="filterObj.arr"
                   v-model="filterObj.arr"
                   placeholder="nothing..."/>
        </Card>
      </i-col>
    </Row>
  </div>
</template>

<script>
/* eslint-disable */

  import { Base64 } from 'js-base64';
  import axios from 'axios';

  export default {
    name: 'homePage',
    data() {
      return {
        re: /(-|)\d{1,3}\.\d+/g,
        re2: /-?\d{1,3}\.\d+(\s*[，,\s]\s*)-?\d{1,3}\.\d+,*/g,
        errorMsg: ['🙄'],

        distance: {
          inVal: '',
          caches: [],
          outVal: [],
          settings: [],
          settingModal: false,
          indeterminate: false,
          checkAll: true,
          checkAllGroup: [],
          checkArr: ['location', 'SN', 'name', 'CD', 'distance', 'autoGPX'],
          serialNumber: 1,
          toFixed: 4,
          outModal: 'go',
          xcodeGpx: 163.7057,
          speed: 2.8
        },
        inLocation: '',

        friends: {
          data: [],
          str: ''
        },

        filterObj: {
          data: '',
          random: [],
          str: '',
          obj: 'rfs21d',
          arr: '',
          length: 0,
          num: 1852000,
          lat: 'z3iafj',
          lng: 'f24sfvs',

        }
      };
    },
    computed: {
      copyDistanceAll() {
        return this.distance.outVal.join('\n');
      },
      isIos() {
        return window.navigator.userAgent.match(/(iPhone|iPod|iPad|ios)/i);
      },
    },
    methods: {
      copySuccess() {
        this.$Message.success('Copied !');
      },
      filterName(str) {
        return str.replace(this.re2, '')
          .replace(/((#|➤)\d+\s*)|(\d+(\.\d+)?km\s*)|(\d+(\.\d+)?(mins|hr|sec))|(\s+$)/ig, '')
          .replace(/(cp\d+\s*)|((❤️|💛|💙|💟)\s*)|(iv\d+\s*)|((\d+:)?\d+:\d+\s*)|\s*$/g, '')
          .replace(/(\s*\(Female\)\s*)|(\s*\(Male\))/)
          .replace(/(https|http):.*\/maps((\?q=)|(\/dir\/Current\+Location\/))/g, '');
      },
      disSetting() {
        this.distance.settingModal = !0;

      },
      disCheckAll() {
        if (this.distance.indeterminate) {
          this.distance.checkAll = false;
        } else {
          this.distance.checkAll = !this.distance.checkAll;
        }
        this.distance.indeterminate = false;

        if (this.distance.checkAll) {
          this.distance.checkAllGroup = this.distance.checkArr;
          localStorage.setItem('distanceSetting', JSON.stringify(this.distance.checkArr));
        } else {
          this.distance.checkAllGroup = [];
          localStorage.setItem('distanceSetting', JSON.stringify([]));
        }
      },
      disCheckAllGroupChange(data) {
        localStorage.setItem('distanceSetting', JSON.stringify(data));
        if (data.length === this.distance.checkArr.length) {
          this.distance.indeterminate = false;
          this.distance.checkAll = true;
        } else if (data.length > 0) {
          this.distance.indeterminate = true;
          this.distance.checkAll = false;
        } else {
          this.distance.indeterminate = false;
          this.distance.checkAll = false;
        }
      },
      getNumber(value) {
        return Number(value) || 1;
      },
      changeLocation() {
        this.formatLocation(this.distance.caches);
      },
      includeDisSetting(str) {
        return this.distance.checkAllGroup.includes(str);
      },
      gpxXml({ $loca, km, v, k, add, fixeds, time }) {
        add = add || 0;
        let lo1 = parseFloat($loca[0]);
        let lo2 = parseFloat($loca[1]);
        let name = this.includeDisSetting('name') ? this.filterName(v) : '';

        return `<wpt lat="${(lo1 + add).toFixed(fixeds)}" lon="${(lo2 + add).toFixed(fixeds)}"><name>,➤${k + this.getNumber(this.distance.serialNumber)} ${name} ${km + 'km'} ${this.locDistance(parseFloat(km))}</name><time>2018-05-26T${time}Z</time></wpt>`;
      },
      formatLocation(valArr) {

        let { toFixed, serialNumber, xcodeGpx, outModal, speed, checkAllGroup } = this.distance;
        let newArr = [];
        let caches = [];
        if (valArr.length) {
          let $valArr = this.includeDisSetting('autoGPX') ? this.allLine(valArr) : this.copyObj(valArr);

          for (let [k, v] of $valArr.entries()) {
            let loca = v.match(this.re2),
              cachesStr = '';

            let name = this.includeDisSetting('name') ? this.filterName(v) : '';
            let $loca = loca[0].match(this.re);
            let location = `${Number($loca[0])
              .toFixed(toFixed)},${Number($loca[1])
              .toFixed(toFixed)}`;

            let km = k > 0 ? $valArr[k - 1].match(this.re) : '';
            km = km ? this.GetDistance($loca[0], $loca[1], km[0], km[1], 1000) : 0;
            let cd = this.includeDisSetting('CD') ? this.locDistance(parseFloat(km)) : '';
            let distance = this.includeDisSetting('distance') && km ? km + 'km' : '';

            let serialNumber = `➤${k + this.getNumber(serialNumber)}`;

            let time = this.getWaite(caches[k - 1], km * (outModal == 'pokeStopGPX' ? xcodeGpx : (1000 / speed)));
            let newPush = '';
            switch (outModal) {
              case 'go':
                newPush = `${this.includeDisSetting('location') ? location + (this.includeDisSetting('location') && checkAllGroup.length == 1 ? '' : ',') : ''}${this.includeDisSetting('SN') ? serialNumber : ''} ${name} ${distance} ${cd}`;
                break;
              case 'pokeStopGPX':
                newPush = this.gpxXml({
                  $loca,
                  k,
                  v,
                  km,
                  fixeds: 6,
                  time
                });
                break;
              case 'pokemonGPX':
                newPush = this.gpxXml({
                  $loca,
                  k,
                  v,
                  km,
                  fixeds: 6,
                  time
                });
                break;
              case 'xcodePatrol':
                newPush = this.gpxXml({
                    $loca,
                    k,
                    v,
                    km,
                    time,
                    fixeds: 6
                  }) +
                  this.gpxXml({
                    $loca,
                    k,
                    v,
                    km,
                    add: 0.00005,
                    fixeds: 6,
                    time: this.getWaite(caches[k - 1], km * xcodeGpx + 0.00001, 5)
                  }) + '\n';
                break;
            }

            cachesStr = `${Number($loca[0])
              .toFixed(6)},${Number($loca[1])
              .toFixed(6)},${serialNumber} ${this.filterName(v)} ${time}`;
            newArr.push(newPush);
            caches.push(cachesStr);
          }
        }

        this.distance.outVal = newArr;
        this.distance.caches = caches;
      },
      getWaite(caches, xcodeGpx, add = 0) {
        let prevKm = caches;
        let time = prevKm != undefined && prevKm.match(/\d+:\d+:\d+/g) ? prevKm.match(/\d+:\d+:\d+/g)[0] : '00:00:00';
        let $h = parseInt(time.split(':')[0]),
          $m = parseInt(time.split(':')[1]),
          $s = parseInt(time.split(':')[2]);

        let $ss = parseInt(xcodeGpx);
        $s = $s + $ss + add;
        if ($s >= 60) {
          $m += parseInt($s / 60);
          $s = $s % 60;
        }
        $s = $s < 10 ? '0' + $s : $s;
        if ($m >= 60) {
          $h += parseInt($m / 60);
          $m = $m % 60;
        }
        $m = $m < 10 ? '0' + $m : $m;
        $h = $h < 10 ? '0' + $h : $h;
        return `${$h}:${$m}:${$s}`;
      },
      async addLocation($val) {
        let val = $val;
        if (val) {
          const outVal = this.distance.caches;
          let valArr = [];
          let hasUrl = val.match(/coding|githubusercontent/ig);
          if (hasUrl) {
            val = await axios.get(val)
              .then((res) => res.data);
          }
          for (let v of val.split('\n')) {
            let m = v.match(this.re);
            if (m && m.length > 1) {
              valArr.push(`${m[0]},${m[1]},${this.filterName(v)}`);
              this.distance.inVal = '';
            }
          }

          !valArr.length ? this.$Message.error(this.errorMsg[0]) : valArr = [...outVal, ...valArr];
          this.formatLocation(valArr);

          // this.$refs.outLocationPs.update();
          this.$nextTick(() => {
            document.querySelector('#outLocationPs').scrollTop = document.querySelector('.out-ul').offsetHeight;
          });
        } else {
          this.$Message.error(this.errorMsg[0]);
        }
      },
      delOutVal(outValIndex) {
        this.distance.caches.splice(outValIndex, 1);
        this.formatLocation(this.distance.caches);
      },

      async getfriends() {
        let f = await axios.get('https://raw.githubusercontent.com/lrb9986/poke/master/friends.txt');
        this.friends.str = f.data;
        f = f.data.split('\n');
        let arr = [];
        for (let i = 0, v; v = f[i++];) {
          let a = v.split(':');
          arr.push({
            default: v,
            number: a[1].trim()
          });
        }
        this.friends.data = arr;
      },

      /**
       * filter pokemongomap.info
       */
      onFilter() {
        let { obj, lat, lng, str, data } = this.filterObj;
        data = data.replace(/\n*$/g, '')
          .split('\n');
        let $data = {};
        for (let i = 0, node; node = data[i++];) {
          $data = {
            ...$data,
            ...JSON.parse(node || '{}')
          };
        }
        let arr = [];

        let $re = new RegExp(str, 'ig');
        for (let v in $data) {
          if ($data[v][obj].match($re)) {
            arr.push(`${this.filterDecode($data[v][lat], 82.7)},${this.filterDecode($data[v][lng], 80.5)},${$data[v][obj]}`);
          }
        }
        this.filterObj.length = arr.length;
        if (arr.length) {
          this.filterObj.arr = this.allLine(arr)
            .join('\n');
        } else {
          this.$Message.warning('nothing');
        }
      },
      filterDecode(val, num) {
        return (Base64.decode(val) * num).toFixed(6);
      },
      allLine(arr) {
        let $arr = this.copyObj(arr);
        let allArr = [];
        let allArrMax = [];
        for (let i = 0, v = $arr.length; i < v; i++) {
          let str = JSON.stringify(this.autoLocat($arr, i));
          allArr.push(str);
        }
        for (let i = 0, v; v = allArr[i++];) {
          let km = v.match(/\d+(\.\d+){0,1}km/g);
          km.sort();
          allArrMax.push(km[km.length - 1]);
        }
        let first = this.copyObj(allArrMax)
          .sort()[0];
        for (let i = 0, v; v = allArrMax[i++];) {
          if (v == first) {
            $arr = JSON.parse(allArr[i - 1]);
            break;
          }
        }
        return $arr;
      },
      autoLocat(arr, index) {
        let _self = this;
        let $arr = this.copyObj(arr);
        let newArr = $arr.sort();
        let $index = index != undefined ? index : parseInt(newArr.length / 2);
        let middle = $arr[$index];
        $arr.splice($index, 1);
        let sortArr = [middle];

        foo(sortArr, $arr, middle);

        function foo(sortArr, arr, range) {
          let desArr = [];
          let loca = range.split(',');

          for (let i = 0, v; v = arr[i++];) {
            let arrLoca = v.split(',');
            desArr.push(_self.GetDistance(loca[0], loca[1], arrLoca[0], arrLoca[1], 1000000));
          }

          let rangeFirst = _self.copyObj(desArr)
            .sort((a, b) => a - b)[0];
          let nextRange;

          for (let i = 0, v; v = desArr[i++];) {
            if (v == rangeFirst) {
              nextRange = `${arr[i - 1]} ${v}km`;
              sortArr.push(nextRange);

              arr.splice(i - 1, 1);
              if (arr.length == 1) {
                sortArr.push(`${arr[0]} ${v}km`);
              }
              break;
            }
          }
          if (arr.length > 1) {
            foo(sortArr, arr, nextRange);
          }
        }

        return sortArr;
      }
    },
    mounted() {
      this.getfriends();
      this.distance.outModal = localStorage.getItem('outModalValue') || this.distance.outModal;
      this.distance.checkAllGroup = JSON.parse(localStorage.getItem('distanceSetting')) || ['location', 'SN', 'name', 'CD', 'distance'];
      this.formatLocation(JSON.parse((localStorage.getItem('locationValue') || '[]')));
    },
    watch: {
      'distance.caches': (v) => {
        localStorage.setItem('locationValue', JSON.stringify(v));
      },
      'distance.outModal': (v) => {
        localStorage.setItem('outModalValue', v);
      }
    }
  };
</script>
